import React, {useMemo, useEffect} from 'react';
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import {IoImagesOutline} from "react-icons/io5";

import styles from './Portfolio.module.scss'
import p1 from '../../assets/images/projects/p1.png';
import p2 from '../../assets/images/projects/p2.jpg';
import p3 from '../../assets/images/projects/p3.jpg';
import p4 from '../../assets/images/projects/p4.jpg';
import p5 from '../../assets/images/projects/p5.jpg';
import p6 from '../../assets/images/projects/p6.png';
import p7 from '../../assets/images/projects/p7.jpg';
import p8 from '../../assets/images/projects/p8.jpg';
import p9 from '../../assets/images/projects/p9.jpg';
import p10 from '../../assets/images/projects/p10.jpg';

import {Title} from "../Title/Title";
import {Card} from "../Card/Card";

export const Portfolio = () => {
    const projects = useMemo(() => [
        {
            id: 1,
            title: 'Vertical Slider',
            link: 'https://aramsuqiasyan.github.io/slider/',
            image: p1
        },
        {
            id: 2,
            title: 'Flex Gallery',
            link: 'https://aramsuqiasyan.github.io/gallery/',
            image: p2
        },
        {
            id: 3,
            title: 'Drag And Drop',
            link: 'https://aramsuqiasyan.github.io/drag/',
            image: p3
        },
        {
            id: 4,
            title: 'Aim Game',
            link: 'https://aramsuqiasyan.github.io/game/',
            image: p4
        },
        {
            id: 5,
            title: 'Funny Board',
            link: 'https://aramsuqiasyan.github.io/board/',
            image: p5
        },
        {
            id: 6,
            title: 'Lambda Restaurant',
            link: 'https://aramsuqiasyan.github.io/lambda/',
            image: p6
        },
        {
            id: 7,
            title: 'Landing Zero',
            link: 'https://aramsuqiasyan.github.io/landingZero/',
            image: p7
        },
        {
            id: 8,
            title: 'Cosmos',
            link: 'https://aramsuqiasyan.github.io/cosmos/',
            image: p8
        },
        {
            id: 9,
            title: 'Social Icons',
            link: 'https://aramsuqiasyan.github.io/socicons/',
            image: p9
        },
        {
            id: 10,
            title: 'Switch',
            link: 'https://aramsuqiasyan.github.io/switch/',
            image: p10
        }
    ], [])

    useEffect(() => {
        const tl = gsap.timeline()
        tl
            .from(".portfolio-card:nth-child(1)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(2)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(3)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(4)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(5)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(6)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(7)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(8)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(9)", {x: 100, scale: 1, opacity: 0,})
            .from(".portfolio-card:nth-child(10)", {x: 100, scale: 1, opacity: 0,})


        ScrollTrigger.create({
            animation: tl,
            trigger: '.portfolio-projects-wrapper',
            start: 'top center',
            end: 'center center',
            scrub: 1,
        })
    }, [])

    return (
        <div id={'portfolio'} className={styles.wrapper}>
            <div className={styles.container}>
                <Title text={'Portfolio'} Icon={IoImagesOutline}/>
                <div className={`${styles.projectsWrapper} portfolio-projects-wrapper`}>
                    <div className={styles.projects}>
                        {
                            projects.map(project => {
                                return <Card
                                    key={project.id.toString()}
                                    title={project.title}
                                    link={project.link}
                                    image={project.image}
                                />
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    );
};