import React, {useEffect, useState} from 'react';
import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import {IoIdCardOutline} from "react-icons/io5";

import styles from './About.module.scss'
import cv from '../../assets/files/ARAM_SUKIASYAN.pdf'
import {Title} from "../Title/Title.jsx";
import {Button} from "../Button/Button";
import {useDownload} from "../../hooks/useDownload";
import {WorkExperience} from "../WorkExperience/WorkExperience";

export const About = () => {
    const [progress, setProgress] = useState(0)
    const {start} = useDownload(({progress, completed}) => {
        setProgress(completed ? 0 : progress)
    }, cv, 'ARAM_SUKIASYAN.pdf')

    const downloadCV = () => {
        start()
    }

    const sendEmail = (email) => {
        let a = document.createElement("a");
        a.href = `mailto:${email}`;
        a.click();
    }

    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const tl = gsap.timeline()
        tl
            .from("#about-image-box",
                {x: -500, opacity: 0})
            .from("#about-who-am-i",
                {x: 500, opacity: 0})
            .from("#about-work-experience",
                {scale: 2, opacity: 0, pointerEvents: 'all'})

        ScrollTrigger.create({
            animation: tl,
            trigger: '#about',
            start: 'top center',
            end: `+=${window.innerHeight / 2}`,
            scrub: 1,
        })
    }, []);


    return (
        <div
            id={'about'}
            className={styles.wrapper}>
            <div className={styles.container}>
                <Title text={'About'} Icon={IoIdCardOutline}/>

                <div className={styles.main}>
                    <div id={'about-image-box'} className={styles.imageBox}>
                        <div className={styles.imageContainer}>
                            <div className={styles.image}/>
                        </div>
                    </div>
                    <div className={styles.information}>
                        <div id={'about-who-am-i'} className={styles.box}>
                            <h3 className={styles.boxTitle}>WHO AM I</h3>
                            <p className={styles.text}>
                                I am a passionate web developer who strives to create fascinating and breathtaking
                                user experiences.With my user oriented approach I make sure to meet deadlines and
                                provide the best value to the project.
                            </p>
                            <div className={styles.buttonsBox}>
                                <Button
                                    showProgress
                                    progress={progress}
                                    text={'Download CV'}
                                    style={{marginRight: 16}}
                                    className={styles.downloadCV}
                                    onClick={downloadCV}/>

                                <Button
                                    text={'Send E-mail'}
                                    onClick={() => sendEmail('aramsuqiasyan20@gmail.com')}/>
                            </div>
                        </div>

                        <div id={'about-work-experience'} className={styles.box}>
                            <h3 className={styles.boxTitle}>WORK EXPERIENCE</h3>
                            <WorkExperience/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};